import { useEffect, useState } from "react";
import { X } from "lucide-react";

export default function EditUserModal({
  user,
  onClose,
  onSave,
  currentUserRole,
}) {
  const [formData, setFormData] = useState(null);

  useEffect(() => {
    if (user) setFormData(user);
  }, [user]);

  if (!user || !formData) return null;

  const handleChange = (e) => {
    setFormData((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const handleSubmit = () => {
    onSave(formData);
    onClose();
  };

  // Determine role options based on current user's permissions
  const roleOptions =
    currentUserRole === "manager"
      ? [
          { value: "admin", label: "ادمین" },
          { value: "manager", label: "مدیر" },
          { value: "user", label: "کاربر" },
        ]
      : [{ value: "user", label: "کاربر" }]; // admin can't promote anyone

  return (
    <div className="fixed inset-0 z-50 bg-black bg-opacity-40 flex items-center justify-center px-4">
      <div className="bg-white dark:bg-neutral-800 w-full max-w-md p-6 rounded-2xl shadow-xl relative">
        <button
          onClick={onClose}
          className="absolute top-4 left-4 text-gray-400 hover:text-red-500"
        >
          <X />
        </button>
        <h3 className="text-xl font-bold mb-6 text-gray-800 dark:text-white">
          ویرایش کاربر
        </h3>

        <div className="space-y-4">
          <input
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder="نام کاربر"
            className="w-full p-2 rounded-xl border dark:bg-neutral-700 dark:text-white"
          />
          <input
            name="email"
            value={formData.email}
            onChange={handleChange}
            placeholder="ایمیل"
            className="w-full p-2 rounded-xl border dark:bg-neutral-700 dark:text-white"
          />
          <input
            name="phone"
            value={formData.phone}
            onChange={handleChange}
            placeholder="شماره تماس"
            className="w-full p-2 rounded-xl border dark:bg-neutral-700 dark:text-white"
          />

          <select
            name="role"
            value={formData.role}
            onChange={handleChange}
            className="w-full p-2 rounded-xl border dark:bg-neutral-700 dark:text-white"
          >
            {roleOptions.map((r) => (
              <option key={r.value} value={r.value}>
                {r.label}
              </option>
            ))}
          </select>
        </div>

        <div className="mt-6">
          <button
            onClick={handleSubmit}
            className="w-full bg-green-600 hover:bg-green-700 text-white py-2 rounded-xl"
          >
            ذخیره تغییرات
          </button>
        </div>
      </div>
    </div>
  );
}
