import { X } from "lucide-react";

const UserDetailModal = ({ user, onClose }) => {
  if (!user) return null;

  return (
    <div className="fixed inset-0 z-50 bg-black bg-opacity-40 flex items-center justify-center px-4">
      <div className="bg-white dark:bg-neutral-800 w-full max-w-md p-6 rounded-2xl shadow-xl relative">
        <button onClick={onClose} className="absolute top-4 left-4 text-gray-400 hover:text-red-500">
          <X />
        </button>
        <h3 className="text-xl font-bold mb-6 text-gray-800 dark:text-white">اطلاعات کاربر</h3>

        <div className="space-y-3 text-sm text-gray-700 dark:text-gray-200">
          <div><span className="font-bold">نام:</span> {user.name}</div>
          <div><span className="font-bold">ایمیل:</span> {user.email}</div>
          <div><span className="font-bold">شماره:</span> {user.phone}</div>
          <div><span className="font-bold">نقش:</span> 
            {user.role === "admin" ? "مدیر" : user.role === "manager" ? "مدیر داخلی" : "کاربر"}
          </div>
          <div><span className="font-bold">وضعیت:</span> {user.status ? "فعال" : "غیرفعال"}</div>
        </div>
      </div>
    </div>
  );
};

export default UserDetailModal;
