import { useState } from "react";
import {
  Eye,
  Pencil,
  Trash,
  ToggleLeft,
  ToggleRight,
  ChevronDown,
  User,
} from "lucide-react";
import { Helmet } from "react-helmet";

// Modal Component
const Modal = ({ isOpen, onRequestClose, onConfirm, userFullName }) => {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
      <div className="bg-white dark:bg-neutral-800 px-6 py-4 rounded-lg shadow-xl max-w-sm mx-4">
        <h2 className="font-bold text-gray-800 dark:text-gray-100 mb-2">
          تأیید حذف
        </h2>
        <p className="text-gray-600 dark:text-gray-300">
          {/* آیا از حذف این کاربر مطمئن هستید؟ */}
          {`آیا از حذف ${userFullName} مطمن هستید`}
        </p>
        <div className="flex justify-end gap-2 mt-4">
          <button
            onClick={onRequestClose}
            className="bg-gray-200 dark:bg-neutral-700 text-gray-800 dark:text-gray-200 font-medium rounded-lg px-4 py-2 hover:bg-gray-300 dark:hover:bg-neutral-600 transition-colors"
          >
            بازگشت
          </button>
          <button
            onClick={onConfirm}
            className="bg-red-500 rounded-lg px-4 py-2 text-white font-medium hover:bg-red-600 transition-colors"
          >
            حذف
          </button>
        </div>
      </div>
    </div>
  );
};

const UsersTable = ({ users, onView, onEdit, onToggleStatus, onDelete }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [userToDelete, setUserToDelete] = useState(null);
  const [expandedUser, setExpandedUser] = useState(null);
  const [currUser, setCurrUser] = useState(null);

  const handleDelete = (userId) => {
    setUserToDelete(userId);
    setIsModalOpen(true);

    let user = users?.find((user) => user.id === userId);

    setCurrUser(user);
  };

  const confirmDelete = () => {
    if (userToDelete) {
      onDelete(userToDelete);
      setUserToDelete(null);
      setIsModalOpen(false);
    }
  };

  const toggleUserDetails = (userId) => {
    setExpandedUser(expandedUser === userId ? null : userId);
  };

  const roleLabels = {
    admin: "ادمین",
    manager: "مدیر",
    user: "کاربر",
  };

  const roleColors = {
    admin:
      "bg-purple-100 text-purple-700 dark:bg-purple-900/30 dark:text-purple-400",
    manager: "bg-blue-100 text-blue-700 dark:bg-blue-900/30 dark:text-blue-400",
    user: "bg-gray-100 text-gray-700 dark:bg-gray-700 dark:text-gray-400",
  };

  return (
    <>
      <Helmet>
        <title>{`کاربران | المکس`}</title>
      </Helmet>

      {/* Desktop table view */}
      <div className="hidden md:block overflow-x-auto bg-white dark:bg-neutral-900 rounded-xl shadow-md">
        <table className="min-w-full text-sm text-right">
          <thead className="bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-gray-200">
            <tr>
              <th className="p-3 font-medium">نام</th>
              <th className="p-3 font-medium">ایمیل</th>
              <th className="p-3 font-medium">شماره</th>
              <th className="p-3 font-medium">نقش</th>
              <th className="p-3 font-medium">وضعیت</th>
              <th className="p-3 font-medium">عملیات</th>
            </tr>
          </thead>
          <tbody className="text-gray-800 dark:text-gray-100 divide-y divide-gray-200 dark:divide-neutral-700">
            {users.length === 0 ? (
              <tr>
                <td
                  colSpan={6}
                  className="p-6 text-center text-gray-500 dark:text-gray-400"
                >
                  هیچ کاربری وجود ندارد
                </td>
              </tr>
            ) : (
              users?.map((user) => (
                <tr
                  key={user.id}
                  className="hover:bg-gray-50 dark:hover:bg-neutral-800 transition-colors"
                >
                  <td className="p-4 font-medium">{`${user.first_name} ${user.last_name}`}</td>
                  <td className="p-4">{user.email}</td>
                  <td className="p-4">{user.phone}</td>
                  <td className="p-4">
                    <span
                      className={`px-2 py-1 rounded-full text-xs font-medium ${
                        roleColors[user.user_type]
                      }`}
                    >
                      {roleLabels[user.user_type] || "مدیر"}
                    </span>
                  </td>
                  <td className="p-4">
                    <button
                      onClick={() => onToggleStatus(user?.id)}
                      className="focus:outline-none flex gap-1 items-center" // Adding flex to align text and toggle
                      aria-label={
                        user.is_active
                          ? "غیرفعال کردن کاربر"
                          : "فعال کردن کاربر"
                      }
                      title={
                        user.is_active
                          ? "غیرفعال کردن کاربر"
                          : "فعال کردن کاربر"
                      }
                    >
                      {user.is_active ? (
                        <ToggleRight
                          size={22}
                          className="text-green-500 hover:text-green-600 mr-2" // Added margin for text spacing
                        />
                      ) : (
                        <ToggleLeft
                          size={22}
                          className="text-gray-400 hover:text-gray-500 mr-2" // Added margin for text spacing
                        />
                      )}

                      {/* Status text */}
                      <span
                        className={`font-medium ${
                          user.is_active ? "text-green-500" : "text-gray-400"
                        }`}
                      >
                        {user.is_active ? "فعال" : "غیرفعال"}
                      </span>
                    </button>
                  </td>
                  <td className="p-4">
                    <div className="flex gap-3">
                      <button
                        onClick={() => onView(user)}
                        className="p-1.5 rounded-full text-blue-500 hover:text-blue-700 hover:bg-blue-50 dark:hover:bg-blue-900/20 transition-colors"
                        aria-label="مشاهده جزئیات کاربر"
                        title="مشاهده"
                      >
                        <Eye size={18} />
                      </button>
                      <button
                        onClick={() => onEdit(user)}
                        className="p-1.5 rounded-full text-amber-500 hover:text-amber-700 hover:bg-amber-50 dark:hover:bg-amber-900/20 transition-colors"
                        aria-label="ویرایش کاربر"
                        title="ویرایش"
                      >
                        <Pencil size={18} />
                      </button>
                      <button
                        onClick={() => handleDelete(user?.id)}
                        className="p-1.5 rounded-full text-red-500 hover:text-red-700 hover:bg-red-50 dark:hover:bg-red-900/20 transition-colors"
                        aria-label="حذف کاربر"
                        title="حذف"
                      >
                        <Trash size={18} />
                      </button>
                    </div>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* Mobile card view */}
      <div className="md:hidden space-y-4">
        {users.length === 0 ? (
          <div className="bg-white dark:bg-zinc-900 rounded-xl shadow-md p-8 text-center">
            <div className="flex flex-col items-center justify-center text-gray-500 dark:text-gray-400">
              <User size={40} className="mb-2 opacity-50" />
              <p>هیچ کاربری وجود ندارد</p>
            </div>
          </div>
        ) : (
          users.map((user) => (
            <div
              key={user.id}
              className="bg-white dark:bg-zinc-900 rounded-xl shadow-md overflow-hidden"
            >
              <div
                className="p-4 flex justify-between items-center cursor-pointer"
                onClick={() => toggleUserDetails(user.id)}
              >
                <div className="flex flex-col">
                  <div className="flex items-center gap-2">
                    <span className="font-bold text-gray-800 dark:text-gray-200">
                      {`${user.first_name} ${user.last_name}`}
                    </span>
                    <span
                      className={`px-2 py-0.5 rounded-full text-xs font-medium ${
                        roleColors[user.role]
                      }`}
                    >
                      {roleLabels[user.role]}
                    </span>
                  </div>
                  <div className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                    {user.email}
                  </div>
                </div>
                <ChevronDown
                  size={20}
                  className={`text-gray-500 transition-transform ${
                    expandedUser === user.id ? "rotate-180" : ""
                  }`}
                />
              </div>

              {expandedUser === user.id && (
                <div className="px-4 pb-4 pt-1 border-t border-gray-100 dark:border-neutral-800">
                  <div className="space-y-3 text-sm">
                    <div className="flex justify-between">
                      <span className="text-gray-500 dark:text-gray-400">
                        شماره تلفن:
                      </span>
                      <span className="text-gray-700 dark:text-gray-300">
                        {user.phone}
                      </span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-500 dark:text-gray-400">
                        وضعیت:
                      </span>
                      <div className="flex items-center">
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            onToggleStatus(user.id); // Call the toggle function
                          }}
                          className="focus:outline-none"
                          aria-label={
                            user.is_active
                              ? "غیرفعال کردن کاربر"
                              : "فعال کردن کاربر"
                          }
                          title={
                            user.is_active
                              ? "غیرفعال کردن کاربر"
                              : "فعال کردن کاربر"
                          }
                        >
                          {user.is_active ? (
                            <div className="flex items-center text-green-500">
                              <ToggleRight size={20} className="ml-1" />
                              <span className="text-xs font-medium">فعال</span>
                            </div>
                          ) : (
                            <div className="flex items-center text-gray-400">
                              <ToggleLeft size={20} className="ml-1" />
                              <span className="text-xs font-medium">
                                غیرفعال
                              </span>
                            </div>
                          )}
                        </button>
                      </div>
                    </div>
                    <div className="grid grid-cols-3 gap-2 pt-2">
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          onView(user);
                        }}
                        className="flex items-center justify-center gap-1 py-2 rounded-lg bg-blue-50 text-blue-600 hover:bg-blue-100 dark:bg-blue-900/20 dark:text-blue-400 dark:hover:bg-blue-900/30 text-xs font-medium"
                      >
                        <Eye size={14} />
                        مشاهده
                      </button>
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          onEdit(user);
                        }}
                        className="flex items-center justify-center gap-1 py-2 rounded-lg bg-amber-50 text-amber-600 hover:bg-amber-100 dark:bg-amber-900/20 dark:text-amber-400 dark:hover:bg-amber-900/30 text-xs font-medium"
                      >
                        <Pencil size={14} />
                        ویرایش
                      </button>
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          handleDelete(user.id);
                        }}
                        className="flex items-center justify-center gap-1 py-2 rounded-lg bg-red-50 text-red-600 hover:bg-red-100 dark:bg-red-900/20 dark:text-red-400 dark:hover:bg-red-900/30 text-xs font-medium"
                      >
                        <Trash size={14} />
                        حذف
                      </button>
                    </div>
                  </div>
                </div>
              )}
            </div>
          ))
        )}
      </div>

      {/* Modal for deletion confirmation */}
      <Modal
        isOpen={isModalOpen}
        onRequestClose={() => setIsModalOpen(false)}
        onConfirm={confirmDelete}
        userFullName={`${currUser?.first_name} ${currUser?.last_name}`}
      />
    </>
  );
};

export default UsersTable;
