import { useEffect, useState } from "react";
import UsersTable from "./UsersTable";
import UserDetailModal from "./UserDetailModal";
import EditUserModal from "./EditUserModal";
import AddUserModal from "./AddUserModal";
import { toast } from "react-hot-toast";

const currentUserRole = "manager";

const UsersPage = () => {
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [editUser, setEditUser] = useState(null);
  const [isAddUserModalOpen, setIsAddUserModalOpen] = useState(false); // ✅ Modal state

  const API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/user/"
      : "https://allmaxsport.com/api/user/";

  useEffect(() => {
    async function loadUsers() {
      const response = await fetch(`${API_URL}`);
      const data = await response.json();
      setUsers(data);
    }
    loadUsers();
  }, []);

  const handleAddUser = async (newUserData) => {
    try {

      const response = await fetch(`${API_URL}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newUserData),
      });

      // Get response details for better debugging
      const responseText = await response.text();
      let responseData;

      try {
        // Try to parse as JSON if possible
        responseData = JSON.parse(responseText);
      } catch (e) {
        // Not JSON, keep as text
        responseData = responseText;
      }

      if (!response.ok) {
        throw new Error("ثبت کاربر با خطا مواجه شد");
      }

      // If we got here, the request was successful
      setUsers((prev) => [...prev, responseData]);
      toast.success("کاربر با موفقیت اضافه شد");
    } catch (err) {
      toast.error("خطا در افزودن کاربر");
    }
  };
  
  const handleDeleteUser = async (userId) => {
    setUsers((prev) => prev.filter((u) => u.id !== userId));
    try {
      const response = await fetch(`${API_URL}?id=${userId}`, {
        method: "DELETE",
      });
      if (!response.ok) return;
      await response.json();
      toast.success("کاربر با موفقیت حذف شد");
    } catch (e) {
      toast.error(e.message);
    }
  };

  const handleStatusToggle = async (userId) => {
    const userToUpdate = users.find((user) => user.id === userId);
    if (!userToUpdate) return toast.error("کاربر پیدا نشد");

    const updatedUser = { ...userToUpdate, is_active: !userToUpdate.is_active };

    try {
      const response = await fetch(`${API_URL}?id=${userId}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ is_active: updatedUser.is_active }),
      });

      if (!response.ok) throw new Error();

      await response.json();
      setUsers((prev) =>
        prev.map((user) => (user.id === userId ? updatedUser : user))
      );
      toast.success("وضعیت کاربر تغییر یافت");
    } catch (error) {
      toast.error("خطا در تغییر وضعیت کاربر");
    }
  };

  const handleUpdateUser = async (updatedUser) => {
    setUsers((prev) =>
      prev.map((user) => (user.id === updatedUser.id ? updatedUser : user))
    );

    await fetch(`${API_URL}?id=${updatedUser.id}`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(updatedUser), // ✅ Ensure correct data
    });

    toast.success("اطلاعات کاربر به‌روزرسانی شد");
  };

  return (
    <div className="space-y-6">
      <div className="flex items-end justify-end">
        <button
          onClick={() => setIsAddUserModalOpen(true)}
          className="bg-blue-600 text-white font-bold rounded-lg px-4 py-2 hover:bg-blue-800 duration-150"
        >
          اضافه کردن کاربر
        </button>
      </div>

      <UsersTable
        users={users}
        onView={setSelectedUser}
        onEdit={setEditUser}
        onToggleStatus={handleStatusToggle}
        onDelete={handleDeleteUser}
      />

      <UserDetailModal
        user={selectedUser}
        onClose={() => setSelectedUser(null)}
      />

      <EditUserModal
        user={editUser}
        onClose={() => setEditUser(null)}
        onSave={handleUpdateUser}
        currentUserRole={currentUserRole}
      />

      <AddUserModal
        isOpen={isAddUserModalOpen}
        onClose={() => setIsAddUserModalOpen(false)}
        onSubmit={handleAddUser}
      />
    </div>
  );
};

export default UsersPage;
