import { useState, useEffect } from "react";
import { X } from "lucide-react";

const initialForm = {
  name: "",
  username: "", // ✅ Add this
  email: "",
  phone: "",
  role: "user",
  password: "",
  confirmPassword: "",
  address: "",
};

const AddUserModal = ({ isOpen, onClose, onSubmit }) => {
  const [formData, setFormData] = useState(initialForm);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    if (isOpen) {
      setFormData(initialForm);
      setErrors({});
      setIsMounted(true);
      setIsSubmitting(false);
    } else {
      setTimeout(() => setIsMounted(false), 300);
    }
  }, [isOpen]);

  const validate = () => {
    const errs = {};
    if (!formData.name.trim()) errs.name = "نام الزامی است";
    if (!formData.username.trim()) errs.username = "نام کاربری الزامی است"; // ✅ added
    if (!formData.email.includes("@")) errs.email = "ایمیل معتبر نیست";
    if (!/^\d{10,15}$/.test(formData.phone))
      errs.phone = "شماره تماس معتبر نیست";
    if (formData.password.length < 6)
      errs.password = "رمز عبور حداقل ۶ حرف باشد";
    if (formData.password !== formData.confirmPassword)
      errs.confirmPassword = "رمز عبور تطابق ندارد";
    return errs;
  };

  const handleChange = (e) => {
    const { name, value } = e.target;

    if (name === "phone") {
      const onlyDigits = value.replace(/\D/g, "");
      setFormData((prev) => ({ ...prev, phone: onlyDigits }));
    } else {
      setFormData((prev) => ({ ...prev, [name]: value }));
    }
  };

  const handleSubmit = async () => {
    const validationErrors = validate();
    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors);
      return;
    }

    setIsSubmitting(true);
    try {
      // Create a clean data object without confirmPassword field
      // Match the exact field names expected by the API
      const apiData = {
        username: formData.username.trim(), // ✅ now using actual username
        email: formData.email.trim(),
        password: formData.password,
        full_name: formData.name.trim(),
        phone_number: formData.phone,
        role: formData.role,
        address: formData.address.trim() || null, // Handle empty address
        is_active: true,
      };

      await onSubmit(apiData);
      setFormData(initialForm);
      onClose();
    } catch (error) {
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleClose = () => {
    setFormData(initialForm);
    setErrors({});
    onClose();
  };

  if (!isMounted) return null;

  return (
    <div className="fixed inset-0 z-50 bg-black bg-opacity-60 flex justify-center items-center px-4">
      <div
        className={`bg-neutral-900 text-white w-full max-w-md rounded-2xl shadow-2xl p-6 relative space-y-6 transform transition-all duration-300 ${
          isOpen ? "opacity-100 scale-100" : "opacity-0 scale-90"
        }`}
      >
        <button
          onClick={handleClose}
          className="absolute top-4 left-4 text-gray-400 hover:text-red-500 transition"
          aria-label="بستن"
        >
          <X size={20} />
        </button>

        <h3 className="text-2xl font-semibold text-center">افزودن کاربر</h3>

        <div className="space-y-4">
          {[
            { name: "name", placeholder: "نام کامل" },
            { name: "username", placeholder: "نام کاربری" }, // ✅ Added
            { name: "email", placeholder: "ایمیل" },
            {
              name: "phone",
              placeholder: "شماره تماس (فقط عدد)",
              maxLength: 15,
            },
            { name: "password", placeholder: "رمز عبور", type: "password" },
            {
              name: "confirmPassword",
              placeholder: "تایید رمز عبور",
              type: "password",
            },
          ].map(({ name, placeholder, type, maxLength }) => (
            <div key={name}>
              <input
                name={name}
                type={type || "text"}
                value={formData[name]}
                onChange={handleChange}
                maxLength={maxLength}
                className={`w-full bg-neutral-800 border ${
                  errors[name] ? "border-red-500" : "border-neutral-700"
                } text-white p-3 rounded-xl focus:outline-none focus:ring-2 ${
                  errors[name] ? "focus:ring-red-500" : "focus:ring-blue-500"
                }`}
                placeholder={placeholder}
              />
              {errors[name] && (
                <p className="text-sm text-red-400 mt-1">{errors[name]}</p>
              )}
            </div>
          ))}

          <textarea
            name="address"
            value={formData.address}
            onChange={handleChange}
            className="w-full bg-neutral-800 border border-neutral-700 text-white p-3 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder="آدرس (اختیاری)"
            rows={2}
          />

          <select
            name="role"
            value={formData.role}
            onChange={handleChange}
            className="w-full bg-neutral-800 border border-neutral-700 text-white p-3 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="user">کاربر</option>
            <option value="admin">ادمین</option>
            <option value="manager">مدیر</option>
          </select>
        </div>

        <div className="flex justify-end pt-4">
          <button
            onClick={handleSubmit}
            disabled={isSubmitting || Object.keys(validate()).length > 0}
            className="bg-blue-600 hover:bg-blue-700 disabled:opacity-50 text-white font-semibold px-6 py-2 rounded-xl transition duration-200"
          >
            {isSubmitting ? "در حال ثبت..." : "ثبت کاربر"}
          </button>
        </div>
      </div>
    </div>
  );
};

export default AddUserModal;
